import _http from './http';

const isDev = process.env.NODE_ENV === 'development';
const devBaseUrl = '/mixServer';
const prodBaseUrl =
  location.host === 'apisdk.lnqwe.com' ? 'https://apisdk2.lnqwe.com' : 'http://api.sdk2.leniugame.com';
const http = _http.create({
  serverType: 'mix',
  baseURL: isDev ? devBaseUrl : prodBaseUrl
});

export const getConfig = params => {
  return http({
    url: '/api/config/get',
    method: 'post',
    params,
    // {mode:0,data:data}
    noMode: true,
    // timeaccess_token
    noTimeAndAccess: true,
    // sign
    noSign: true,
    // ret0
    isInitCheck: true
  });
};

export const init = params => {
  return http({
    url: '/sdk/app/initialize',
    method: 'post',
    params,
    // timeaccess_token
    noTimeAndAccess: true,
    // sign
    noSign: true,
    // ret0
    isInitCheck: true
  });
};

