<template>
  <div class="ln-login">
    <Form ref="form">
      <Field v-model="username" placeholder="" :rules="[{ required: true, message: '' }]">
        <i slot="left-icon" class="iconfont iconuser"></i>
      </Field>
      <Password v-model="password"></Password>
      <div class="bottom">
        <Row class="login-row">
          <Button class="login-btn" block type="info" @click="login">
            
          </Button>
        </Row>

        <Row class="login-row" gutter="10">
          <Col span="12"><Button block type="info" native-type="button" @click="guestLogin"></Button></Col>
          <Col span="12"><Button block type="info" @click="changeComponent('PhoneRegister')"></Button></Col>
        </Row>

        <Row class="login-row link-wrap">
          <!-- <span class="link" @click="changeComponent('PhoneLogin')"></span> -->
          <span class="link" @click="changeComponent('ForgetPassword')">?</span>
        </Row>
      </div>
    </Form>
  </div>
</template>

<script>
import cookies from '@/cookies';
import { Form, Field, Button, Col, Row, Dialog } from 'vant';
import { commonMixin } from '@/mixin';
import Password from '@/components/Password';
//  
import server from '@/server';
import channelServer from '@/server/channelServer';
export default {
  name: 'ln-login',
  mixins: [commonMixin],
  components: { Form, Field, Button, Col, Row, Password },
  data() {
    return {
      username: '',
      password: ''
    };
  },
  created() {},
  mounted() {
    const cookieUserName = cookies.getItem('ln_h5sdk_user_name');
    if (cookieUserName) {
      this.username = cookieUserName;
    }
  },
  methods: {
    // 
    login() {
      this.formCheck('form').then(() => {
        const params = {
          uname: this.username,
          upass: this.password
        };
        server.login(params);
      });
    },
    guestLogin() {
      channelServer.guestLogin();
    }
  }
};
</script>
