import _http from './http';

const isDev = process.env.NODE_ENV === 'development';
const devBaseUrl = '/channelServer';
const prodBaseUrl = location.host === 'apisdk.lnqwe.com' ? ' https://apisdk.lnqwe.com' : 'http://api.sdk.leniugame.com';
const http = _http.create({
  serverType: 'channel',
  baseURL: isDev ? devBaseUrl : prodBaseUrl
});

// 
export const getWxPayUrl = params => {
  return http({
    url: '/api/config/auth',
    method: 'get',
    params,
    // {mode:0,data:data}
    noMode: true,
    // timeaccess_token
    noTimeAndAccess: true
  });
};
// 
export const getConfig = params => {
  return http({
    url: '/api/config/get',
    method: 'post',
    params,
    // {mode:0,data:data}
    noMode: true,
    // timeaccess_token
    noTimeAndAccess: true,
    // sign
    noSign: true,
    // ret0
    isInitCheck: true
  });
};

// 
export const init = params => {
  return http({
    url: '/v4/app/initialize',
    method: 'post',
    params,
    // timeaccess_token
    noTimeAndAccess: true,
    // sign
    noSign: true,
    // ret0
    isInitCheck: true
  });
};

