// 
const pay = {
  isShow: null,
  init() {
    const payWrap = document.querySelector('#ln-pay');
    payWrap.addEventListener('transitionend', () => {
      this.handleTransitionEnd();
    });
    const closeBtn = document.querySelector('#pay-close');
    closeBtn.addEventListener('click', () => {
      this.close();
    });
  },
  handleTransitionEnd() {
    if (this.isShow === false) {
      // 
      const payFrame = document.querySelector('#payFrame');
      payFrame.src = '';
    }
  },
  show(url) {
    this.isShow = true;
    const payFrame = document.querySelector('#payFrame');
    payFrame.src = url;
    const payWrap = document.querySelector('#ln-pay');
    payWrap.classList.add('is-show');
  },
  close() {
    this.isShow = false;
    const payWrap = document.querySelector('#ln-pay');
    payWrap.classList.remove('is-show');
  },
  weixin(params) {
    if (!WeixinJSBridge) {
      console.error('WeixinJSBridgeWeixinJSBridge.invoke');
    } else {
      const { url, ...invokeParams } = params;
      console.log('invokeParams');
      console.log(invokeParams);
      WeixinJSBridge.invoke(
        'getBrandWCPayRequest',
        invokeParams,
        // {
        //   appId: 'wx2421b1c4370ec43b', // 
        //   timeStamp: '1395712654', // 1970
        //   nonceStr: 'e61463f8efa94090b1f366cccfbbb444', // 
        //   package: 'prepay_id=u802345jgfjsdfgsdg888',
        //   signType: 'MD5', // 
        //   paySign: '70EA570631E4BB79628FBCA90534C63FF7FADD89' // 
        // },
        function(res) {
          if (res.err_msg == 'get_brand_wcpay_request:ok') {
            // ,
            // res.err_msgok
            const payFrame = document.querySelector('#payFrame');
            payFrame.src = url;
          }
        }
      );
    }
  }
};

export default pay;
